.shop {
    padding: 40px 0 100px 0;

    @media screen and (max-width: 600px) {
        padding: 0 0 100px 0;
    }

}

.shop-container {
    padding: 50px 0 50px 0;

    
    @media screen and (min-width: 1000px) and (max-width: 1300px) {
        .section-shop {
            margin-left: 120px;
        }
    }
    

    @media screen and (max-width: 1000px) {
        flex-direction: column;
        align-items: center;

        .section-shop {
            max-width: 100% !important;
            padding: 0 40px !important;
        }

        .section-shop-img {
            display: none !important;
        }
    }

    .section-shop, .section-shop-img {
        display: block;

        .shop-text {
            font-size: 20px;
            line-height: 1.4em;
            max-width: 450px;
        }

        .section-sub-header {
            font-size: 48px;
            font-weight: bold;
            margin: 20px 0 20px 0;
            color: var(--black);        
        }

        .button-container {
            margin-top: 150px;
            text-align: right;
            right: -30px;

            @media screen and (max-width: 600px) {
                right: 50px;
                margin-top: 60px;
            }

            .indie-btn {
                width: 250px;

                @media screen and (max-width: 600px) {
                    width: 170px;
                }

                &::before {
                    width: calc(100% + 48%);
                    left: -420px;

                    @media screen and (max-width: 600px) {
                        left: 0;
                        width: calc(100% - 30%);
                    }
                }
            }
        }

        .section-title {
            margin-bottom: 0;
            
            @media screen and (max-width: 600px) {
                font-size: 120px;
                text-align: right;
            }

            .coffee-beans {
                position: relative;
                top: 110px;
                height: 100px;
                left: -70px;
                width: 260px;

                @media screen and (max-width: 600px) {
                    left: -15px;
                }
            }
        }

        .image-container {
            height: 650px;
            width: 650px;
            z-index: -5;

            @media screen and (min-width: 1000px) and (max-width: 1300px) {
                width: 450px;
                height: 450px;
            }

            img {
                height: 650px;
                width: 650px;

                @media screen and (min-width: 1000px) and (max-width: 1300px) {
                    width: 450px;
                    height: 450px;
                }
            }
        }

        .image-container::before {
            content: "";
            position: absolute;
            z-index: 2;
            width: 650px;
            height: 650px;
            background: #0000003d;

            @media screen and (min-width: 1000px) and (max-width: 1300px) {
                width: 450px;
                height: 450px;
            }
          }
    }

}
